@font-face {
	font-family: "iconFont";
	src: url('../fonts/iconFont.eot');
	src: url('../fonts/iconFont.eot?#iefix') format('eot'),
		url('../fonts/iconFont.woff2') format('woff2'),
		url('../fonts/iconFont.woff') format('woff'),
		url('../fonts/iconFont.ttf') format('truetype'),
		url('../fonts/iconFont.svg#iconFont') format('svg');
}

@mixin icon-styles {
	font-family: "iconFont", sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	font-style: normal;
	font-variant: normal;
	font-weight: normal;
	// speak: none; // only necessary if not using the private unicode range (firstGlyph option)
	text-decoration: none;
	text-transform: none;
}

%icon {
	@include icon-styles;
}

@function icon-char($filename) {
	$char: "";

	@if $filename == arrow-long {
		$char: "\E001";
	}
	@if $filename == arrow-right {
		$char: "\E002";
	}
	@if $filename == corner {
		$char: "\E003";
	}
	@if $filename == eye {
		$char: "\E004";
	}
	@if $filename == facebook {
		$char: "\E005";
	}
	@if $filename == instagram {
		$char: "\E006";
	}
	@if $filename == like {
		$char: "\E007";
	}
	@if $filename == mute {
		$char: "\E008";
	}
	@if $filename == pause {
		$char: "\E009";
	}
	@if $filename == play {
		$char: "\E00A";
	}
	@if $filename == share {
		$char: "\E00B";
	}
	@if $filename == sound {
		$char: "\E00C";
	}
	@if $filename == twitter {
		$char: "\E00D";
	}
	@if $filename == youtube {
		$char: "\E00E";
	}

	@return $char;
}

@mixin icon($filename, $insert: before, $extend: true) {
	&:#{$insert} {
		@if $extend {
			@extend %icon;
		} @else {
			@include icon-styles;
		}
		content: icon-char($filename);
	}
}

.icon-arrow-long {
	@include icon(arrow-long);

}
.icon-arrow-right {
	@include icon(arrow-right);

}
.icon-corner {
	@include icon(corner);

}
.icon-eye {
	@include icon(eye);

}
.icon-facebook {
	@include icon(facebook);

}
.icon-instagram {
	@include icon(instagram);

}
.icon-like {
	@include icon(like);

}
.icon-mute {
	@include icon(mute);

}
.icon-pause {
	@include icon(pause);

}
.icon-play {
	@include icon(play);

}
.icon-share {
	@include icon(share);

}
.icon-sound {
	@include icon(sound);

}
.icon-twitter {
	@include icon(twitter);

}
.icon-youtube {
	@include icon(youtube);

}